import React from "react";
import Toolbar from "../components/TopToolbar";
import { Box, Grid } from "@mui/material";

class Investors extends React.Component {
  render() {
    return <Box sx={{ flexGrow: 1 }}>
      <Toolbar />
      <Grid container justifyContent="center">
        <Grid item xs={8}>
          <h1>Be Part of the New Tech Future</h1>
          <p>
            Viridity Capital LLC offers modern and efficient solutions for website
            development, backend solutions, databases, and other related
            technologies. We focus on providing innovative solutions that are
            improvements over antiquated solutions, and can help businesses improve their online
            presence and operational efficiency. With a team of experienced developers and designers, Viridity
            Capital LLC uses cutting-edge technologies to create custom software
            solutions that meet the unique needs of our clients. We specialize
            in building full-stack solutions, that are scalable, secure and high-performing.
            Our goal is to help businesses grow and succeed in today's digital world by
            providing them with the best technology solutions.
          </p>

          <p>
            Join this unique opportunity to revolutionize how tech is used in our
            world, and become an investor.
          </p>
        </Grid>
      </Grid>
    </Box>
  }
}

export default Investors;
